<template>
	<div style="display: flex; justify-content: center; align-items: center;  height: 100vh;">
		<div style="display: flex; justify-content: space-around">
			<div style="margin-top: -100px;">
				<a-form-model :model="user" :label-col="labelCol" :wrapper-col="wrapperCol" style="width: 500px">
					<a-form-model-item style="display: flex; justify-content: center; margin: 0 0 -5px 80px;">
						<a-upload name="file" list-type="picture-card" class="avatar-uploader" :show-upload-list="false" :action="this.$store.state.requestPath + 'uploadHeadPic'"
						 :before-upload="beforeUpload" @change="handleChange">
							<a-avatar :size="100" v-if="imageUrl" :src="imageUrl" />
							<!-- <img v-if="imageUrl" :src="picRequestPath + 'headpic/' + imageUrl" alt="avatar" style="border-radius: 50%; width: 128px; height: 128px;" /> -->
							<div v-else>
								<a-icon :type="loading ? 'loading' : 'plus'" />
								<div class="ant-upload-text">
									修改
								</div>
							</div>
						</a-upload>
					</a-form-model-item>
					<a-form-model-item label="用户名">
						<a-input v-model="user.name" disabled />
					</a-form-model-item>
					<!-- 
					<a-form-model-item label="密码">
						<a-input v-model="user.password" />
					</a-form-model-item> -->
					<a-form-model-item label="性别">
						<a-radio-group v-model="user.gender">
							<a-radio value="M">
								男
							</a-radio>
							<a-radio value="F">
								女
							</a-radio>
						</a-radio-group>
					</a-form-model-item>
					<a-form-model-item label="年龄段">
						<a-select :default-value="ageList[user.age]" @change="handleAgeChange">
							<a-select-option :value=key v-for="(value, key) in ageList">
								{{value}}
							</a-select-option>
						</a-select>
					</a-form-model-item>
					<a-form-model-item label="职业">
						<a-select :default-value="occupationList[user.occupation]" @change="handleOccupationChange">
							<a-select-option :value=key v-for="(value, key) in occupationList">
								{{value}}
							</a-select-option>
						</a-select>
					</a-form-model-item>
					<a-form-model-item label="邮编">
						<a-input v-model="user.zipCode" />
					</a-form-model-item>
					<a-form-model-item style="display: flex; justify-content: space-around;">
						<a-button type="primary" @click="onSubmit" :disabled="this.flag>0 ? false : true">
							提交
						</a-button>
						<a-button style="margin-left: 20px;" @click="onBack">
							返回
						</a-button>
					</a-form-model-item>
				</a-form-model>
			</div>

		</div>
	</div>
</template>
<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'

	function getBase64(img, callback) {
		const reader = new FileReader();
		reader.addEventListener('load', () => callback(reader.result));
		reader.readAsDataURL(img);
	}
	export default {
		computed: {
			...mapState(['userInfo', 'requestPath', 'picRequestPath'])
		},
		data() {
			return {
				flag: -1,
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},
				user: {
					"name": "undefine",
					"password": "undefine",
					"gender": "undefine",
					"age": null,
					"occupation": null,
					"zipCode": "undefine",
					"headPic": "default.jpg"
				},
				ageList: null,
				occupationList: null,
				loading: false,
				imageUrl: '',
				latestImageUrl: null,
			};
		},
		watch: {
			user: {
				handler: function(val, oldval) {
					this.flag = this.flag + 1;
				},
				deep: true //对象内部的属性监听，也叫深度监听
			},
		},
		async created() {
			Object.assign(this.user, this.userInfo);
			this.imageUrl = this.picRequestPath + 'headpic/' + this.user.headPic;
			this.latestImageUrl = this.imageUrl;
			this.getAllAge();
			this.getAllOccupation();
		},
		
		methods: {
			...mapMutations(['updateUserInfo']),
			async getAllAge(){
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allAge');
					this.ageList = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			async getAllOccupation(){
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allOccupation');
					this.occupationList = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			handleAgeChange(value){
				this.user.age = value;
			},
			handleOccupationChange(value){
				this.user.occupation = value;
			},
			async userUpdate() {
				console.log(this.user)
				let {
					data
				} = await this.axios.post(this.requestPath + 'userUpdate/', this.user);
				if (data.code == 0) {
					this.$message.success(' (｡･∀･)ﾉﾞ 修改成功~ ', 1);
					this.updateUserInfo(this.user);  // 更新全局数据
					this.onBack();
				} else {
					this.$message.error(' (￣△￣；) 出错啦~ ', 2);
				}
			},
			async onSubmit() {
				if (this.flag > 0 && this.userInfo) {
					this.userUpdate();
				}
			},
			onBack() {
				this.user.pic = this.latestImageUrl;
				this.imageUrl =  this.latestImageUrl;
				this.$router.push({
					path: '/home'
				});
			},
			handleChange(info) {
				if (info.file.status === 'uploading') {
					this.loading = true;
					return;
				}
				if (info.file.status === 'done') {
					getBase64(info.file.originFileObj, imageUrl => {
						this.imageUrl = imageUrl;
						this.loading = false;
					});
					this.user.headPic = info.file.response.data.src;
				}
			},
			beforeUpload(file) {
				const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
				if (!isJpgOrPng) {
					this.$message.error('You can only upload JPG file!');
				}
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('Image must smaller than 2MB!');
				}
				return isJpgOrPng && isLt2M;
			},
		},
	};
</script>

<style>
	.avatar-uploader>.ant-upload {
		border-radius: 50%;
	}

	.ant-upload-select-picture-card i {
		font-size: 16;
		color: #999;
	}

	.ant-upload-select-picture-card .ant-upload-text {
		margin-top: 8px;
		color: #666;
	}
</style>
